import React, { Fragment } from "react";

import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

import DatePickerFrom from "./DatePickerFrom";
import DatePickerTo from "./DatePickerTo";

const toReport = history => {
  history.push({
    pathname: "/report"
  });
};

const Report = ({
  title,
  button,
  dateFrom,
  dateFromChange,
  dateTo,
  history,
  dateToChange,
  getReport,
  isAdmin
}) => (
  <Fragment>
    <Typography classes={{ root: title }} variant="title" gutterBottom>
      Отчет
    </Typography>
    <DatePickerFrom {...{ dateFrom, dateFromChange }} />
    <DatePickerTo {...{ dateTo, dateToChange }} />
    <br />
    <Button
      classes={{ root: button }}
      variant="outlined"
      color="primary"
      onClick={() => toReport(history)}
    >
      Отчет
    </Button>
  </Fragment>
);

export default Report;
