import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import fetchPost from "./services/fetch";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Message from "./Message";

const styles = () => ({
  button: {
    marginTop: "2.5%"
  },
  title: {
    paddingTop: "5%"
  },
  authStyle: {
    alignSelf: "center",
    textAlign: "center"
  }
});
class Auth extends Component {
  state = { login: "", pass: "", error: false };
  enterLogin = event => {
    this.setState({ login: event.target.value });
  };
  enterPass = event => {
    this.setState({ pass: event.target.value });
  };
  auth = async () => {
    const { setAuth, setIsAdmin, history } = this.props;
    const { login, pass } = this.state;

    const { auth, isAdmin } = await fetchPost(
      "/auth",
      JSON.stringify({ login, pass })
    );

    if (auth === "ok") {
      this.setState({ error: false });
      setAuth(true);
      setIsAdmin(isAdmin);
      history.push({
        pathname: "/"
      });
    } else {
      setAuth(false);
      this.setState({ error: true });
    }
  };
  errorClose = () => {
    this.setState({ error: false });
  };
  render() {
    const {
      classes: { button, title, authStyle }
    } = this.props;

    const { login, pass, error } = this.state;
    const { enterLogin, enterPass, auth, errorClose } = this;
    return (
      <div className={authStyle}>
        <Typography classes={{ root: title }} variant="title" gutterBottom>
          Авторизация
        </Typography>

        <TextField
          label="Логин"
          value={login}
          onChange={enterLogin}
          margin="normal"
        />
        <br />
        <TextField
          label="Пароль"
          value={pass}
          onChange={enterPass}
          margin="normal"
          type="password"
        />
        <br />
        <Button
          classes={{ root: button }}
          variant="outlined"
          color="primary"
          onClick={auth}
        >
          Войти
        </Button>
        <br />
        <Message
          open={error}
          handleClose={errorClose}
          message="Неправильный логин или пароль."
          variant="error"
        />
      </div>
    );
  }
}

export default withStyles(styles)(Auth);
