import React, { Component } from "react";
import compose from "recompact/compose";
import { withStyles } from "@material-ui/core/styles";
import { registerLocale } from "react-datepicker";
import withLogout from "./HOCs/withLogout";

import checkSession from "./HOCs/checkSession";

import CheckTicket from "./CheckTicket";
import Report from "./Report";
import Loading from "./Loading";

import "react-datepicker/dist/react-datepicker.css";
import { ru } from "date-fns/locale";
registerLocale("ru", ru);

const styles = () => ({
  button: {
    marginTop: "2.5%"
  },
  title: {
    marginTop: "15%"
  },
  ticketsClass: {
    marginTop: "5%",
    alignSelf: "center",
    textAlign: "center"
  }
});
class Tickets extends Component {
  async componentDidMount() {
    const { loadDate, checkIsAdmin } = this.props;
    await checkIsAdmin();

    loadDate();
  }
  render() {
    const {
      sessionChecking,
      isAdmin,
      classes: { button, title, ticketsClass },
      ticket,
      error,
      success,
      dateFrom,
      dateTo,
      enterTicket,
      checkTicket,
      dateFromChange,
      dateToChange,
      messageClose,
      history
    } = this.props;
    if (sessionChecking) {
      return <Loading />;
    }
    return (
      <div className={ticketsClass}>
        {!isAdmin && (
          <CheckTicket
            {...{
              title,
              ticket,
              enterTicket,
              button,
              checkTicket,
              error,
              success,
              messageClose
            }}
          />
        )}
        <Report
          {...{
            title,
            button,
            dateFrom,
            dateFromChange,
            dateTo,
            dateToChange,
            history,
            isAdmin
          }}
        />
      </div>
    );
  }
}

export default compose(
  withLogout,
  withStyles(styles),
  checkSession
)(Tickets);
