import React, { Fragment } from "react";

import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";

import MessageSuccess from "./MessageSuccess";
import MessageError from "./MessageError";

const CheckTicket = ({
  title,
  ticket,
  enterTicket,
  button,
  checkTicket,
  error,
  success,
  messageClose
}) => (
  <Fragment>
    <Typography classes={{ root: title }} variant="title" gutterBottom>
      Проверка номера
    </Typography>
    <TextField
      label="Номер"
      value={ticket}
      onChange={enterTicket}
      margin="normal"
    />
    <br />
    <br />
    <Button
      classes={{ root: button }}
      variant="outlined"
      color="primary"
      onClick={checkTicket}
    >
      Проверить
    </Button>
    <br />
    <MessageError {...{ open: error, messageClose }} />
    <MessageSuccess {...{ open: success, messageClose }} />
  </Fragment>
);
export default CheckTicket;
