import React, { Component } from "react";

import fetchPost from "./services/fetch";
import { HashRouter, Route, Switch } from "react-router-dom";
import Auth from "./Auth";
import Tickets from "./Tickets";
import ReportTable from "./ReportTable";
import "./App.css";
import { loadState, saveState, removeState } from "./services/localStorage";

class App extends Component {
  state = {
    auth: false,
    isAdmin: false,
    ticket: "",
    error: false,
    success: false,
    dateFrom: null,
    dateTo: null,
    report: []
  };

  setAuth = auth => {
    this.setState({ auth });
  };
  setIsAdmin = isAdmin => {
    this.setState({ isAdmin });
  };
  enterTicket = event => {
    this.setState({ ticket: event.target.value, error: false, success: false });
  };
  checkTicket = async () => {
    const { ticket } = this.state;
    if (ticket.length) {
      const { ticket: answerTicket } = await fetchPost(
        "/ticket",
        JSON.stringify({ ticket })
      );

      if (answerTicket === "ok") {
        this.setState({ error: false, success: true });
      } else {
        this.setState({ error: true, success: false });
      }
    }
  };
  setReport = report => {
    this.setState({ report });
  };
  dateFromChange = dateFrom => {
    this.setState({ dateFrom });
    saveState("dateFrom", dateFrom);
  };
  dateToChange = dateTo => {
    this.setState({ dateTo });
    saveState("dateTo", dateTo);
  };
  loadDate = () => {
    this.setState({
      dateFrom: loadState("dateFrom"),
      dateTo: loadState("dateTo")
    });
  };
  messageClose = () => {
    this.setState({ error: false, success: false });
  };
  closeTable = () => {
    this.setState({ report: [] });
  };
  resetDate = () => {
    this.setState({
      dateFrom: new Date(),
      dateTo: new Date()
    });
    removeState("dateFrom");
    removeState("dateTo");
  };
  render() {
    const {
      auth,
      isAdmin,
      ticket,
      error,
      success,
      dateFrom,
      dateTo,
      report
    } = this.state;
    const {
      setAuth,
      setIsAdmin,
      enterTicket,
      checkTicket,
      setReport,
      dateFromChange,
      dateToChange,
      messageClose,
      closeTable,
      resetDate,
      loadDate
    } = this;
    return (
      <HashRouter>
        <Switch>
          <Route
            exact
            path="/"
            render={props => (
              <Tickets
                {...{
                  auth,
                  isAdmin,
                  ticket,
                  error,
                  success,
                  dateFrom,
                  dateTo,
                  enterTicket,
                  checkTicket,
                  setIsAdmin,
                  dateFromChange,
                  dateToChange,
                  messageClose,
                  resetDate,
                  loadDate,
                  ...props
                }}
              />
            )}
          />
          <Route
            path="/auth"
            render={props => <Auth {...{ setAuth, setIsAdmin, ...props }} />}
          />
          <Route
            path="/report"
            render={props => (
              <ReportTable
                {...{
                  ...props,
                  report,
                  closeTable,
                  isAdmin,
                  setIsAdmin,
                  setReport
                }}
              />
            )}
          />
        </Switch>
      </HashRouter>
    );
  }
}

export default App;
